import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue"
import uniPopup from "@/components/uni-popup/uni-popup.vue"
export default{
	components: {uniLoadMore,uniPopup},
	data(){
		return{
			shopObj:{},
			isReason:false,
			Reason:"",//原因
			isSales:false,//修改销量的显示和隐藏
			reason:"",//驳回原因
			selesNum:"",//新增月销量
			type:1,//1查询全部商品,2代审核,3未通过
			status:"loading",
			delState:1,//1为撤销，2为删除
			delIsShow:false,//撤销,删除弹框的显示
			stockShow:false,//库存弹框的显示
			stockNum:"",//库存数量
			shareShow:false,//分享
			statusArr:["","出售中","待审核","已售罄","已下架","查看原因","已撤销"],//商品状态
			showPopupBottomShare:false,
			proList:[],
			proStatus:"",//商品状态
			bottomData:[
				{
					src:"../../static/shareImg/info_weibo@2x.png",
					text:"微博"
				},
				{
					src:"../../static/shareImg/info_wechat@2x.png",
					text:"微信"
				},
				{
					src:"../../static/shareImg/info_friends@2x.png",
					text:"朋友圈"
				}
				// {
				// 	src:"../../static/shareImg/info_qq@2x.png",
				// 	text:"QQ"
				// },
				// {
				// 	src:"../../static/shareImg/info_qqzone@2x.png",
				// 	text:"QQ空间"
				// }
			],
			pageNo:1,
			proId:"",//操作的商品id
			pageAll:2,
			productMonthSaleSum:""
		}
	},
	methods:{
		//查看原因
		seeReason(productNote){
			console.log(productNote)
			if(productNote){
				let that=this;
				that.isReason=!that.isReason;
				that.Reason=productNote;
			}
			
		},
		//修改销量的的显示
		isSalesChange(id,num){
			this.selesNum="";
			if(id){
				this.productMonthSaleSum=num
				this.proId=id;
			}else{
				this.proId="";
			}
			this.isSales=!this.isSales;
		},
		//销量的修改
		sales(){
			let that=this;
			if(that.selesNum){
				that.$ajax({
					url:"/shopProduct/updateShopProductIsOnSale",
					data:{
						isOnSale:7,
						productId:that.proId,
						shopId:that.shopObj.shopId,
						sumStock:that.selesNum
					},success(d){
						that.isSales=!that.isSales;
						
						uni.showToast({
							title:"修改成功",
							icon:"none"
						})
					}
				})
			}else{
				uni.showToast({
					title:"请输入要修改的月销量",
					icon:none,
				})
			}
		},
		//撤销，删除弹框的显示
		delStateChange(index,id,status){
			this.delState=index;
			this.proId=id;
			this.proStatus=status;
			this.delIsShow=!this.delIsShow;
		},
		//库存弹框的显示
		stock(id){
			this.proId=id;
			this.stockShow=!this.stockShow
		},
		//分享隐藏
		hidePopup(index){
			this.showPopupBottomShare=!this.showPopupBottomShare;
			if(index){
				this.proId=index
			}
		},
		//预览
		seePro(index){
			uni.navigateTo({
				url:"/pages/pro/proSee?productId="+index
			})
		},
		//分享
		shareFun(e){
			let that=this;
			var index=e.currentTarget.dataset.index*1;
			let img="";
			let title="";
			let summary="";
			let href="http://www.hnlxyj.com/wx/html/mall/details.html?id="+that.proId;
			for(let i in that.proList){
				if(that.proId==that.proList[i].productId){
					img=that.proList[i].productImg;
					summary=that.proList[i].productName;
					break;
				}
			}
			switch(index){
				case 0:
					//微博
					uni.share({
						provider: "sinaweibo",
						type: 0,
						href: href||"",
						title: title||"",
						summary: summary||"",
						imageUrl: img||"",
						success: function (res) {
							console.log("success:" + JSON.stringify(res));
						},
						fail: function (err) {
							uni.showToast({
								title:"请安装微博后再分享",
								icon:"none"
							})
							console.log("fail:" + JSON.stringify(err));
						}
					});
					break;
				case 1:
					//微信
					uni.share({
						provider: "weixin",
						scene: "WXSceneSession",
						type: 0,
						href: href||"",
						title: title||"",
						summary: summary||"",
						imageUrl: img||"",
						success: function (res) {
							console.log("success:" + JSON.stringify(res));
						},
						fail: function (err) {
							uni.showToast({
								title:"请安装微信后再分享",
								icon:"none"
							})
							console.log("fail:" + JSON.stringify(err));
						}
					});
					break;
				case 2:
					//朋友圈
					uni.share({
						provider: "weixin",
						scene: "WXSenceTimeline",
						type: 0,
						href: href,
						title: title,
						summary: summary,
						imageUrl: img,
						success: function (res) {
							console.log("success:" + JSON.stringify(res));
						},
						fail: function (err) {
							uni.showToast({
								title:"请安装微信后再分享",
								icon:"none"
							})
							console.log("fail:" + JSON.stringify(err));
						}
					});
					break;
				case 3:
					//QQ
					uni.share({
						provider: "qq",
						type: 3,
						href: href,
						title: title,
						summary: summary,
						imageUrl: img,
						success: function (res) {
							console.log("success:" + JSON.stringify(res));
						},
						fail: function (err) {
							console.log("fail:" + JSON.stringify(err));
						}
					});
					break;
				case 4:
					//QQ空间
					uni.share({
						provider: "qq",
						type: 3,
						href: href,
						title: title,
						summary: summary,
						imageUrl: img,
						success: function (res) {
							console.log("success:" + JSON.stringify(res));
						},
						fail: function (err) {
							console.log("fail:" + JSON.stringify(err));
						}
					});
					break;
			}
		},
		commit(){
			let that=this;
			//1撤销，2删除弹框的显示
			if(that.delState==1){
				that.proStatusFun(that.proStatus,that.proId);
				this.delIsShow=!this.delIsShow;
			}else{
				that.$ajax({
					url:"/shopProduct/deleteShopduct",
					method:"POST",
					data:{
						productId:that.proId
					},success(d){
						that.delIsShow=!that.delIsShow;
						for(let i in that.proList){
							if(that.proId==that.proList[i].productId){
								 that.proList.splice(i, 1);
								 break;
							}
						}
						uni.showToast({
							title:"删除成功",
							icon:"none"
						})
					}
					
				})
			}
		},
		//商品状态的修改
		proStatusFun(index,proId){
			let that=this;
			this.$ajax({
				url:"/shopProduct/updateShopProductIsOnSale",
				method:"POST",
				data:{
					isOnSale:index,
					productId:proId,
					shopId:that.shopObj.shopId
				},
				success(d){
					for(let i in that.proList){
						if(proId==that.proList[i].productId){
							that.proList[i].isOnSale=d.isOnSale
						}
					}
				}
			})
		},
		commitStock(){
			//库存提交
			let that=this;
			this.stockShow=!this.stockShow
			this.$ajax({
				url:"/shopProduct/updateShopProductsumStock",
				method:"POST",
				data:{
					productId:that.proId,
					sumStock:that.stockNum
				},success(d){
					for(let i in that.proList){
						if(that.proId==that.proList[i].productId){
							that.proList[i].sumStock=d.sumStock;
							that.proList[i].isOnSale=d.isOnSale;
							return false;
						}
					}
				}
			})
		},
		init(index){
			this.type=index;
			this.pageNo=1;
			this.pageAll=2;
			this.status="loading";
			this.search()
		},
		//查询数据
		search(){
			let that=this;
			if(that.pageNo<=that.pageAll){
				this.$ajax({
					url:"/shopProduct/selectShopProduct",
					method:"POST",
					data:{
						shopId:that.shopObj.shopId,
						type:that.type,
						pageNo:that.pageNo
					},
					success(d){
						that.pageNo=that.pageNo*1+1;
						that.pageAll=d.totalPage;
						if(that.pageNo==2){
							that.proList=d.lists||[]
						}else{
							let arr=that.proList;
							that.proList=that.proList.concat(d.lists)
						}
						if(that.pageNo>that.pageAll){
							that.status="noMore"
						}
					},
				})
			}	
		},
		//编辑商品
		edit(index){
			uni.navigateTo({
				url:"/pages/pro/proAdd?proId="+index
			})
		}
	},
	//下拉加载
	onReachBottom(){
		this.search()
	},
	onLoad(){
		let that=this;
		uni.getStorage({
			key:"shopObj",
			success(res){
				that.shopObj=JSON.parse(res.data);
			}
		})
	},
	onShow(){
		this.init(1);
	},
	onNavigationBarButtonTap(e){
		uni.navigateTo({
			url:"/pages/pro/proAdd"
		})
	}
}
